.pokemon-details {
  .flex-container {
    display: flex;
    justify-content: space-around;
    align-items: center;
  }

  .pokemon-base-stats {
    .stats-container {
      width: 100%;
      display: flex;
      flex-direction: column;

      .stats-item {
        flex-direction: row;
        display: flex;
        padding: 5px 0;
        border-bottom: 1px solid grey;

        .stat-name {
          width: 100px;
        }

        .stat-value {
          width: 100px;
        }

        .stat-graph {
          flex-grow: 1;

          .graph {
            background-color: #59ba4c;
            height: 20px;
            border-radius: 5px;
          }
        }
      }
    }

    
  }

  .pokedex-data {
    table {
      th {
        text-align: right;
        vertical-align: top;
        padding-right: 10px;
      }

      td {
        text-align: left;
        vertical-align: top;
      }

      ol {
        padding-inline-start: 20px;
        margin-block-start: 0;
        margin-block-end: 0;
      }
    }
  }
}